import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {OtpProvider} from "../../providers/otp/otp";

/**
 * Generated class for the UserSettingsRefillKeysPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-user-settings-refill-keys',
  templateUrl: 'user-settings-refill-keys.html',
})
export class UserSettingsRefillKeysPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, private otp: OtpProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad UserSettingsRefillKeysPage');
  }

  refillKeys() {
    this.otp.copyKeyFromUSB();
  }
}



